.container > div > div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text {
  pointer-events: none;

  /* justify-content: start !important;
  /* font-family: 'Kanit', sans-serif; */
  /* line-height: 0px; */
  /* text-transform: uppercase; */
  cursor: pointer;
}
.slopeBegin {
  background-color: #20232f;
  /* clip-path: polygon(20% 0, 70% 0, 50% 100%, 0% 100%); */
}
#spring-title {
  font-size: 10em;
  color: #545864;
  /* z-index: 1; */
}

#spring-title span {
  display: inline-block;
  position: relative;
}
.spring-button-container {
  position: absolute;
  display: flex;
  width: 10%;
  justify-content: space-around;
  bottom: 10%;
  left: 45vw;
  background-color: silver;
  z-index: 2;
}

.slopeBegin,
.interface-layer {
  position: absolute;
  width: 100%;
  height: 100%;
  /* z-index: -1; */
  /* border: 2px solid white; */
}

#pink {
  background: linear-gradient( deeppink 0%, coral 100%);
}

#teal {
  background: linear-gradient(to right, SlateBlue 0%, DeepSkyBlue 100%);
}

#tomato {
  background: linear-gradient(to right, tomato 0%, gold 100%);
}
@media screen and (max-width: 1200px) {
    #spring-title {
      font-size: 4em;
    }
  }
  
@media screen and (max-width: 600px) {
  #spring-title {
    font-size: 2em;
  }
}
